@keyframes rotReact {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(20deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
@keyframes sizeReact {
  0% {
    width: 40px;
    height: 40px;
  }
  50% {
    width: 30px;
    height: 30px;
  }
  100% {
    width: 40px;
    height: 40px;
  }
}

