/* search box css */

.search-result-box {
  margin-top: 5px;
  position: absolute;
  max-width: 300px;
  max-height: 350px;
  overflow: auto;
  background-color: #5c6866;
  color: black;
  border-radius: 5px;
  z-index: 10000;
}

.search-result-box-collapse {
  position: absolute;
  overflow: auto;
  background-color: #5c6866;
  color: black;
  z-index: 10000;
}

.btn-search-collapse {
  background-color: inherit;
  color: white;
}

.search-box-input {
  margin-right: 20px;
  border: none;
  background-color: rgb(6, 34, 37);
  border-bottom: 2px solid #077869;
  padding: 5px;
}

.search-box-input-collapse {
  font-size: 15px;
  margin-right: 20px;
  border: none;
  background-color: rgb(6, 34, 37);
  border-bottom: 2px solid #077869;
  padding: 5px;
  color: white;
  width: 100%;
}

.search-result-box-item {
  text-align: left;
  color: rgb(219, 219, 219);
  padding: 20px 10px 20px 10px;
  border-bottom: 2px solid rgb(36, 44, 36);
  margin-top: 3px;
  margin-bottom: 3px;
  margin-left: 10px;
  margin-right: 10px;
}

.search-result-box-item:hover {
  background-color: rgb(51, 62, 63);
  color: white;
  border-radius: 5px;
}

.search-result-box-item-link {
  text-decoration: none;
  font-weight: bold;
  color: white;
  font-size: 15px;
}

.search-result-box-item-link:hover {
  text-decoration: none;
  color: white;
}

.search-result-tag-box {
  font-size: 15px;
  font-style: italic;
}

.reaction:hover {
  /* fill: green; */
  width: 40px;
  height: 40px;
  transition: width 0.5s, height 0.5s, fill 0.2s;
  animation-name: rotReact;
  animation-duration: 1s;
  animation-iteration-count: 1;
}
.reaction {
  width: 30px;
  height: 30px;
  transition: width 0.5s, height 0.5s, fill 0.2s;
}

a:active .reaction {
  transition: fill 0.2s;
  animation-name: sizeReact;
  animation-duration: 0.2s;
  animation-iteration-count: 1;
}

